export default {
	data() {
		return {
			list: []
		};
	},
	onLoad (options) {
		this.list = JSON.parse(options.list);
	},
	methods: {
		downloadImage (item) {  // 下载图片并保存到相册
			uni.showActionSheet({
					itemList: ['保存到相册'],
					success: function (res) {
						if(res.tapIndex == 0) {
							uni.downloadFile({
								url: item,
								success: function (res) {
									if(res.statusCode == 200) {
										uni.saveImageToPhotosAlbum({
											filePath: res.tempFilePath,
											success: function (result) {
												uni.showToast({
													title: '保存成功'
												})
											}
										})
									}
								}
							})
						}
					}
			});
		}
	}
}
